.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  max-width: 650px;
  width: 100%;
  width: calc(100% - 1em);
  box-sizing: border-box;
  overflow-x: hidden;
  font-size: 13px;
}

a {
  color: inherit;
  text-decoration: underline;
}

#content-links {
  display: flex;
}